.profile {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.overBlock {
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  margin-bottom: 20px;
}

.infoBlock {
  width: 80%;
  display: flex;
  justify-content: space-between;
  background-color: transparent !important;
  border-radius: 4px;
  margin-right: 0;
}

@media (max-width: 425px) {
  .overBlock {
    justify-content: flex-start;
    flex-direction: column;
    text-align: center;
  }
  .overBlock > div:nth-of-type(2) {
    max-width: 100% !important;
    margin-bottom: 10px;
  }
  .infoBlock {
    width: 100%;
    flex-direction: column;
    background-color: transparent !important;
  }
}
